              </div>
              <div class="item-in">
                <div v-for="item in aboutList" :key="item.id" class="list-btn" @click="goLink('/about/' + item.id )">
                  {{ item.title }}
                </div>
              </div>
            </div>
            <div :class="{'list-item': true, active: active2 }">
              <div class="list-btn" @click="active2 = !active2">
                <span>Courses</span>
                <img :src="nav_1">
              </div>
              <div class="item-in">
                <div class="list-btn" @click="goLink('/courses/1')">Chemistry</div>
                <div class="list-btn" @click="goLink('/courses/2')">Biology</div>
                <div class="list-btn" @click="goLink('/courses/3')">Lab Safety</div>
                <div class="list-btn" @click="goLink('/courses/4')">Ecology</div>
                <div class="list-btn" @click="goLink('/courses/5')">Earth Science</div>
              </div>
            </div>
            <div class="list-item">
              <div class="list-btn" @click="goLink('/news')">
                <span>News</span>
              </div>
            </div>
            <div class="login-btn-box">
              <div v-if="!token" class="login-btn" @click="goLink('/loginAndSignUp')">Login / Sign up</div>
              <div v-else class="login-btn" @click="logout">Logout</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import logo from '@/assets/logo.png'
import nav_1 from '@/assets/nav_1.png'
import { getActiveAbouts } from '@/api'
import { getToken, removeToken } from '@/cookie/token'
export default {
  data() {
    const token = getToken()
    return {
      logo,
      nav_1,
      active: false,
      active1: false,
      active2: false,
      aboutList: [],
      token
    }
  },
  created() {
    getActiveAbouts().then(res => {
      this.aboutList = res
    }).catch(() => {})
  },
  methods: {
    goLink(path) {
      this.$router.push({ path })
      this.active = false
    },
    logout() {
      removeToken()
      this.goLink('/')
      setTimeout(() => {
        window.location.reload()
      }, 1000)
    }
  }
}
</script>
